<template>
  <div id="home">
    <nav-bar class="home-nav-bar">
      <div slot="left">l</div>
      <div slot="center">c</div>
      <div slot="right">r</div>
    </nav-bar>
    <tab-control
      :titles="['流行','新款','精选']"
      class="tab-c"
      @tabClick="tabClick"
      ref="tabcontrol2"
      v-show="isTabControlFix"
    ></tab-control>
    <better-scroll
      class="home-content"
      ref="scrolltop"
      :probetype="3"
      :pullUpLoad="true"
      @cscroll="contentScroll"
      @pullUpLoad="contentPUL"
    >
      <home-swiper :banners="banners" @swiperload="swiperload"></home-swiper>
      <home-recommend :recommends="recommends"></home-recommend>
      <Feature-view></Feature-view>
      <tab-control :titles="['流行','新款','精选']" @tabClick="tabClick" ref="tabcontrol1"></tab-control>
      <goods-list :goods="goods[currenttype].list"></goods-list>
    </better-scroll>

    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>
  </div>
</template>

<script>
import navBar from "components/common/navbar/NavBar";
import goodsList from "components/common/goodslist/GoodsList";
import betterScroll from "components/common/betterscroll/BetterScroll";

import tabControl from "components/content/tabcontrol/TabControl";

import homeSwiper from "./childComps/HomeSwiper";
import homeRecommend from "./childComps/HomeRecommend";
import FeatureView from "./childComps/FeatureView";

import { getHomeMultidata, getHomeData } from "network/home";

import { debounce } from "common/utils.js";
import { imgloadMix,backTopMix } from "common/mixsin.js"

export default {
  name: "Home",
  components: {
    navBar,
    goodsList,
    betterScroll,
    tabControl,
    homeSwiper,
    homeRecommend,
    FeatureView
  },
  data() {
    return {
      banners: [],
      recommends: [],
      goodstype: ["pop", "new", "sell"],
      currenttype: "pop",
      isTabControlFix: false,
      offsettop: 0,
      saveY: 0,
      imgListener: null,
      goods: {
        pop: {
          page: 0,
          list: []
        },
        new: {
          page: 0,
          list: []
        },
        sell: {
          page: 0,
          list: []
        }
      }
    };
  },
  computed: {},
  mixins: [imgloadMix,backTopMix],
  methods: {
    swiperload() {
      this.offsettop = this.$refs.tabcontrol1.$el.offsetTop;
    },
    //点击事件相关
    tabClick(index) {
      // console.log(index);
      this.currenttype = this.goodstype[index];

      this.$refs.tabcontrol1.currentIndex = index;
      this.$refs.tabcontrol2.currentIndex = index;
    },


    //监听滚动事件
    contentScroll(position) {
      //backTop是否显示
      this.isShowBackTop = -position.y > 1000;

      //tabcontrol吸顶效果
      this.isTabControlFix = -position.y > this.offsettop;
      
    },
    //上拉加载更多
    contentPUL() {
      this.getHomeData(this.currenttype);
      this.$refs.scrolltop.finshUpLoad();
    },

    //网络请求相关
    getHomeMultidata() {
      getHomeMultidata()
        .then(res => {
          // console.log(res);
          this.banners = res.data.banner.list;
          this.recommends = res.data.recommend.list;
        })
        .catch(err => {});
    },

    getHomeData(type) {
      const page = this.goods[page] + 1;
      getHomeData(type, 1)
        .then(res => {
          console.log(res);
          this.goods[type].list.push(...res.data.list);
          this.goods[type].page++;
        })
        .catch();
    }
  },
  created() {
    this.getHomeMultidata();
    this.getHomeData("pop");
    this.getHomeData("sell");
    this.getHomeData("new");
  },
  mounted() {
    // 1. 上拉加载更多

    // 定义一个变量来存储这个方法，可以在离开当前也面时取消全局事件的监听
    const refresh = debounce(this.$refs.scrolltop.refresh, 100);
    this.imgListener = () => {
      refresh()
    }
    this.$bus.$on("itemimageLoad", this.imgListener);
  },
  activated () {
    this.$refs.scrolltop.refresh()
    // console.log("activated");
    this.$refs.scrolltop.ScrollTo(0,this.saveY,0)
  },
  deactivated () {
    this.saveY = this.$refs.scrolltop.getScrollY()

  },
  destroyed () {
    //因为keep-alive 所有需要在destroyed中取消对全局事件的监听
    this.$bus.$off("itemimageLoad", this.imgListener)
    
  }
};
</script>
  
<style  scoped>
#home {
  /* padding-top: 44px; */
  /* margin-bottom: 49px; */
  /* padding-bottom: 49px; */
  height: 100vh;
  position: relative;
}

.home-nav-bar {
  background-color: pink;
  color: white;
  text-align: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 9;
  z-index: 9;
}

.tab-c {
  /* position: sticky; */
  /* top: 44px; */
  background-color: #fff;
  position: relative;
  top: 44px;
  z-index: 9;
}

.home-content {
  overflow: hidden;

  position: absolute;
  top: 44px;
  bottom: 49px;
  left: 0;
  right: 0;
}
</style>