<template>
  <div class="tab-control">
    <div :key="index" v-for="(item,index) in titles" class="tab-control-item"  @click="tabcontrolclick(index)" >
      <span :class="{active: index === currentIndex}">{{item}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TabControl",
  props: {
    titles: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data () {
    return {
      currentIndex: 0
    }
  },
  methods: {
    tabcontrolclick(index) {
      this.currentIndex = index;
      this.$emit('tabClick',index);
    }
  }
};
</script>

<style  scoped>
  .tab-control {
    width: 100%;
    height: 40px;
    line-height: 40px;
    /* background-color: #fff;/ */
    display: flex;
    text-align: center;
    z-index: 9;

  }
   .tab-control-item{
     flex: 1;
   }

  .tab-control-item span {
    padding: 5px 0 5px 0;
  }
   .active {
     color: pink;
      border-bottom: 3px solid pink;
   }
</style>